.searchResults {
    position: absolute;
    background: #fff;
    border-radius: 4px;
    margin-top: 64px;
    width: 600px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.16);
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    .resultItem {
        padding: 12px;
        display: flex;
        align-items: center;
        .tag {
            background: rgb(0, 113, 194);
            border-radius: 4px;
            font-size: 12px;
            font-weight: 500;
            height: 22px;
            padding: 0px 4px;
            width: 70px;
            height: 20px;
            text-align: center;
            color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .tagAirport {
            background: rgb(255, 128, 0);
            color: #000;
        }
        .description {
            flex-grow: 1;
            padding-left: 12px;
            .title {
                font-size: 14px;
                font-weight: 400;
            }
            .subText {
                font-size: 12px;
                font-weight: 500;;
            }
        }

        &:hover {
            background: rgba(18, 115, 196, 0.06);
            cursor: pointer;
        }
    }
    .noResults {
        padding: 12px;
        font-size: 14px;
        font-weight: 400;
    }
}

li[aria-selected="true"] {
    background: rgba(18, 115, 196, 0.06);
}
