.container {
    width: 100%;
    height: calc(-260px + 100vh);
    max-height: 596px;
    background: url(../images/background.jpg);
    background-size: cover;
    background-position-x: 50%;
    background-position-y: 50%;
    
    .gradient {
        width: 100%;
        height: 100%;
        background: linear-gradient(rgba(24,121,202,1),rgba(24,121,202,0));
        .subContainer {
            margin: 0px 325px;
            .title {
                font-family: Avenir Next,BlinkMacSystemFont,-apple-system,Segoe UI,Roboto,Helvetica,Arial,sans-serif;
                font-weight: 700;
                display: inline-block;
                color: #fff;
                font-size: 40px;
            }
            .featuresContainer {
                display: flex;
                .featureContainer {
                    display: flex;
                    color: #fff;
                    font-size: 20px;
                    margin-right: 8px;
                    align-items: center;
                    .feature {
                        margin: 0px 8px;
                    }
                }
            }
        }
    }
}