.header {
    display: flex;
    height: 64px;
    background: #1879ca;
    .headerContent {
        display: flex;
        width: 100%;
        height: 45px;
        margin: 0px 325px;
        margin-top: 8px;
        align-items: center;
        .logo {
            flex-grow: 1;
        }
        .currency {
            width: 36px;
            margin-right: 8px;
            color: #fff;
            font-size: 14px;
            cursor: pointer;
        }
        .language {
            width: 36px;
            margin-right: 8px;
            cursor: pointer;
            img {
                width: 24px;
                height: 24px;
                border-radius: 50%;
            }
        }
        .manageBooking {
            button {
                background: #ffff;
                cursor: pointer;
                border: 0px;
                padding: 8px 16px;
                border-radius: 4px;
                color: rgb(18, 115, 196);
            }
        }
    }
}
