.searchBar {
    display: flex;
    flex: 1;
    border-right: 10px solid #ffb700;
    .icon {
        align-self: center;
        margin: 0px 12px;
    }
    .searchInput {
        flex-grow: 1;
        input {
            width: 100%;
            height: 100%;
            padding: 0;
            border: 0;
            font-size: 16px;
            font-weight: 500;
            outline: 0;
        }
    }
    .searchResults {
        position: absolute;
    }
    .loader {
        width: 40px;
        height: 100%;
        svg {
            transform: scale(0.3);
            transform-origin: top left;
            margin-top: 14px;
        }
    }
}