.searchForm {
    display: flex;
    width: 100%;
    height: 80px;
    border: 10px solid #ffb700;
    margin-top: 32px;
    border-radius: 4px;
    box-sizing: border-box;
    background: #fff;
    .searchButton {
        width: 100px;
        height: 100%;
        background: #068323;
        border: 0;
        color: #fff;
        font-size: 20px;
        font-weight: 700;
    }
}